/* App.css */

.App {
  font-family: Arial, sans-serif;
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  background-color: #f0f0f0;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

h1 {
  text-align: center;
  color: #333;
}

.horiz {
  text-align: right;
}

.progress-container {
  margin-bottom: 10px;
}

.progress-label {
  margin-bottom: 5px;
}

.progress-bar-container {
  width: 100%;
  background-color: #ddd;
  height: 20px;
  margin-top: 5px;
  border-radius: 4px;
  overflow: hidden;
}

.progress-bar {
  height: 100%;
  background-color: #4caf50;
  text-align: right;
  padding-right: 5px;
  line-height: 20px;
  box-sizing: border-box;
  overflow: hidden;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
}

table th, table td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

table th {
  background-color: #f2f2f2;
}

table tr:nth-child(even) {
  background-color: #f9f9f9;
}

table tr:hover {
  background-color: #e9e9e9;
}

.completed {
  text-decoration: line-through;
}
